html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: white;
}

a {
  text-decoration: none;
  color: #b3d82b;
}

li {
  padding: 0.2rem;
}

p {
  font-family: "Open Sans", sans-serif;
}

.section-title {
  color: #5B2A84;
  font-weight: bold;
}

.section-link {
  font-size: 0.8rem;
  color: #231F20;
}
.section-link:hover {
  color: #b3d82b;
}

.news-tags {
  color: #411763;
}

.news-title {
  color: #231F20;
}

.news-date {
  color: #424242;
}

.calendar-date {
  color: #411763;
}

.calendar-month {
  color: #231F20;
}

.calendar-event {
  color: #411763;
}

.calendar-time {
  color: #424242;
}

.content-column h1, .content-column h2, .content-column h3, .content-column h4, .content-column h5, .content-column h6 {
  color: #5B2A84;
}

.is-style-kings-file-block,
.editor-styles-wrapper .is-kings-file-block {
  background-color: #e5ebc3;
  padding: 1rem;
  margin-bottom: 4px;
}
.is-style-kings-file-block a:visited,
.editor-styles-wrapper .is-kings-file-block a:visited {
  color: #231F20;
}
.is-style-kings-file-block:hover,
.editor-styles-wrapper .is-kings-file-block:hover {
  background-color: #b3d82b;
}
.is-style-kings-file-block:hover a:hover,
.editor-styles-wrapper .is-kings-file-block:hover a:hover {
  color: white;
}
.is-style-kings-file-block:hover a::before,
.editor-styles-wrapper .is-kings-file-block:hover a::before {
  color: white;
}
.is-style-kings-file-block:hover > *,
.editor-styles-wrapper .is-kings-file-block:hover > * {
  color: white;
}
.is-style-kings-file-block a,
.editor-styles-wrapper .is-kings-file-block a {
  color: #231F20;
  font-weight: 500;
  display: flex;
  align-items: center;
}
.is-style-kings-file-block a::before,
.editor-styles-wrapper .is-kings-file-block a::before {
  font: var(--fa-font-solid);
  content: "\f15b";
  font-size: 1rem;
  color: #424242;
  padding-right: 1rem;
}

.is-style-kings-quote-block,
.editor-styles-wrapper .is-kings-quote-block {
  margin-top: 1.8rem;
  margin-bottom: 1.8rem;
  text-align: center;
  padding-left: 4rem;
  padding-right: 4rem;
}
.is-style-kings-quote-block p,
.editor-styles-wrapper .is-kings-quote-block p {
  font-family: "Cormorant", serif;
  font-weight: 300 !important;
}

.custom #my-search {
  float: right;
  width: 306px;
}

.custom form.search_form_visible {
  width: 306px;
}

.custom form.search_form_visible #s {
  background: #fff;
  border: 1px solid #000;
  font-size: 12px;
  margin: 0 6px 0 0;
  padding: 4px;
  width: 215px;
  float: left;
}

.custom #my-search input#searchsubmit.my-search {
  background: #000;
  color: #fff;
  cursor: pointer;
  float: right;
  padding: 4px;
}

.kings-latest-posts img {
  margin-bottom: 0.5rem;
}
.kings-latest-posts li {
  border: #F5F5F5 1rem solid;
  box-shadow: 5px 10px 18px #888888;
  padding: 0;
  min-height: fit-content;
}
.kings-latest-posts a.wp-block-latest-posts__post-title {
  color: #5B2A84;
  font-weight: bold;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}
.kings-latest-posts a.wp-block-latest-posts__post-title:visited {
  color: #5B2A84;
}
.kings-latest-posts a.wp-block-latest-posts__post-title, .kings-latest-posts time, .kings-latest-posts div.wp-block-latest-posts__post-excerpt {
  padding-left: 0.8rem;
  padding-right: 0.8rem;
}