@use 'globals' as g;



// File Block

.is-style-kings-file-block, 
.editor-styles-wrapper .is-kings-file-block { 
	background-color: g.$green-light;
	padding: 1rem;
	margin-bottom: 4px;

	a:visited{
		color: g.$dark;
	}



	&:hover{
		background-color: g.$green;

		a:hover{
			color: white;
		}

		a::before{
			color: white;
		}

		

		&>*{
			color: white;
		}

		
		

	}

	




	a{
		color: g.$dark;
		font-weight: 500;
		
		display: flex;
		align-items: center;


		


	}
	a::before {
		font: var(--fa-font-solid);
		content: "\f15b";
		font-size: 1rem;
		color: g.$muted;
		padding-right: 1rem;


		
		

	
	}
	
}

// Quote Block

.is-style-kings-quote-block, 
.editor-styles-wrapper .is-kings-quote-block { 

	margin-top: 1.8rem;
	margin-bottom: 1.8rem;
	text-align: center;
	padding-left: 4rem;
	padding-right: 4rem;

	p{
		font-family: 'Cormorant', serif;
    	font-weight: 300 !important;
		
		//font-style: italic;

	}

	
	
}

// Search Edit

.custom #my-search { float:right; width:306px; }

.custom form.search_form_visible { width: 306px; }

.custom form.search_form_visible #s {
    background:#fff;
    border: 1px solid #000;
    font-size:12px;
    margin:0 6px 0 0;
    padding:4px;
    width:215px;
	float:left;
}

.custom #my-search input#searchsubmit.my-search {
    background: #000;
    color: #fff;
    cursor: pointer;
    float: right;
    padding: 4px;
}


//latest posts block

.kings-latest-posts{

	img{
		margin-bottom: 0.5rem;
	}

	li{
		border: g.$light 1rem solid;
		box-shadow: 5px 10px 18px #888888;
		padding: 0;
		min-height: fit-content;
		
	}

	a.wp-block-latest-posts__post-title{
		color:g.$purple;
		font-weight: bold;
		font-size: 0.9rem;
		margin-bottom: 1.5rem;

		&:visited{
			color:g.$purple;
		}
	}

	a.wp-block-latest-posts__post-title, time, div.wp-block-latest-posts__post-excerpt{
		padding-left: 0.8rem;
		padding-right: 0.8rem;
	}

}